import * as React from 'react';
import { Input } from 'antd';
import { EnhancedTable } from 'oned-react';
import { ColumnProps } from 'oned-react/components/EnhancedTable/EnhancedTable';

const columns = [
  {
    title: 'Full Name',
    width: 100,
    dataIndex: 'name',
    key: 'name',
    fixed: 'left' as 'left',
  },
  { title: 'Age', width: 100, dataIndex: 'age', key: 'age' },
  { title: 'Column 1', dataIndex: 'address', key: '1', width: 200 },
  { title: 'Column 2', dataIndex: 'address', key: '2', width: 200 },
  { title: 'Column 3', dataIndex: 'address', key: '3', width: 200 },
  {
    title: 'Column 4',
    dataIndex: 'address',
    key: '4',
    width: 200,
    type: 'component',
    render: text => {
      return <Input value={text} onChange={() => {}} />;
    },
  },
  {
    title: 'Column 8',
    dataIndex: 'address',
    key: '8',
    width: 200,
    type: 'text-two' as 'text-two',
    render: () => {
      return (
        <div className="two-line-cell">
          <div className="first-line">第一行</div>
          <div className="second-line">第二行</div>
        </div>
      );
    },
  },
  {
    title: 'Action',
    key: 'operation',
    width: 100,
    render: () => <a href="#">action</a>,
  },
] as Array<ColumnProps<any>>;

const data = [];
for (let i = 0; i < 20; i++) {
  data.push({
    key: i,
    name: `Edrward ${i}`,
    age: 32,
    address: `London Park no. ${i}`,
  });
}

class Simple extends React.Component {
  render() {
    return <EnhancedTable pagination={false} dataSource={data} scroll={{ y: 400 }} columns={columns} />;
  }
}

export default <Simple />;

